<template>
  <!-- eslint-disable-next-line vue/max-attributes-per-line -->
  <div v-if="treeData">
    <div class="cheader">Segments</div>
    <svg></svg>
  </div>
</template>

<script>
/* eslint-disable */
import * as d3 from 'd3'

export default {
  name: 'tree-map',
  data () {
    return {
      line: '',
      tree: [],
      parsedTree: {},
      colors: ['#f6a39f',
        '#5ecee9',
        '#fd9cba',
        '#90d1a4',
        '#eaa5c3',
        '#dbe9a8',
        '#dcb4e5',
        '#b1bf81',
        '#a4bfeb',
        '#f6d89e',
        '#79cac4',
        '#ecb489',
        '#9dede5',
        '#e3b298',
        '#c3f3ca',
        '#e7b2b7',
        '#a6cca7',
        '#ccba8f',
        '#e3e2b9',
        '#bbc49a'],
      selected: null,
      sum: null
    }
  },
  props: ['treeData', 'height', 'width', 'selectedBar'],
  watch: {
    treeData(newTree) {
      if (!this.selectedBar) {
        console.log(this.treeData)
        this.clearChart()
        this.etl()
        this.renderChart()
        this.renderChart()
      } else {
        this.handleSelection()
      }
    }
  },
  mounted () {

    this.etl()
    if (this.parsedTree) {
      this.renderChart()
    }
  },
  methods: {
    clearChart() {
      this.svg.selectAll("g").remove()
    },
    renderChart () {
      var me = this
      this.svg = d3.select(this.$el).select('svg').attr("width", this.width).attr("height", this.height)
      let width = this.width
      let height = this.height

      const fader = function(color) { return d3.interpolateRgb(color, "#fff")(0.2); }
//      const color = d3.scaleOrdinal(d3.schemeCategory20.map(fader))


      const format = d3.format(",d")

      this.treemap = d3.treemap()
        .tile(d3.treemapResquarify)
        .size([width, height])
        .round(true)
        .paddingInner(3)

      this.root = d3.hierarchy(this.parsedTree)
        .eachBefore(function(d) {
          d.data.id = d.data.name;
        })
        .sum(this.sumBySize)
        .sort(function(a, b) {
          return b.height - a.height || b.value - a.value;
        })

      this.treemap(this.root)

      this.cell = this.svg.selectAll("g")
        .data(this.root.leaves())
        .enter().append("g")
        .on("click", function (d) {
          console.log(d)
          if (d3.select(this).attr("class") !== 'leaf active' || !(d3.select(this).attr("class"))) {
            d3.select(this).attr("class","leaf active")
            me.cellClicked(d, this)
            d3.select(this).selectAll("text").attr("class","text")
          }
          else {
            d3.select(this).attr("class","leaf not")
            me.cellClicked(d)
            d3.select(this).selectAll("text").attr("class","text")
          }
        })
        .attr("transform", function(d) { return "translate(" + d.x0 + "," + d.y0 + ")"; })

      this.rect = this.cell.append("rect")
        .attr("id", function(d) { return d.data.id; })
        .attr("width", function(d) { return d.x1 - d.x0; })
        .attr("height", function(d) { return d.y1 - d.y0; })
        .attr("class", function(d) {
          if (me.selectedBar) {
            if (d.data.id === me.selectedBar) {
              return "leaf active"
            } else {
              return "leaf not"
            }

          } else {
            return "leaf not"
          }
        })
        .attr("fill", function(d) {
          return d.data.color;
        });

      this.cell.append("clipPath")
        .attr("id", function(d) {
          return "clip-" + d.data.id; })
        .append("use")
        .attr("xlink:href", function(d) { return "#" + d.data.id; })

      this.cell.append("text")
        .attr("clip-path", function(d) { return "url(#clip-" + d.data.id + ")"; })
        .selectAll("tspan")
        .data(function(d) {
          let g =  d.data.percent + '%'
          //        return d.data.id.split(/(?=[A-Z][^A-Z])/g);
          return [d.data.id, g];
        })
        .enter().append("tspan")
        .attr("x", 4)
        .attr("y", function(d, i) { return 13 + i * 10; })
        .text(function(d) { return d; });

      this.cell.append("title")
        .text(function(d) {
          return d.data.id + "\n" + format(d.value); })
    },
    handleSelection () {
      this.rect.selectAll()
        .attr("class", function(d) {
          if (me.selectedBar) {
            if (d.data.id === me.selectedBar) {
              return "leaf active"
            } else {
              return "leaf not"
            }

          } else {
            return "leaf not"
          }
        })
        .attr("fill", function(d) {
          return d.data.color;
        });
    },
    etl () {
      var me = this
      let g = this.treeData
      console.log(g)
      let td = {
        children: [
        ]
      }

      let totalScope = d3.sum(g, function (d) {
        return d.customers
      })

      let ct = 0
      let co = 0

      while (ct < g.length) {
        let dg = g[ct]
        if (co > 19) {
          co = 0
        }
        let ccolor = me.colors[co]
        let j = {
          id: dg.segment,
          name: dg.segment,
          children: [
            {
              count: dg.customers,
              percent: Math.floor((dg.customers / totalScope)*1000)/10,
              id: dg.segment,
              name: dg.segment,
              color: ccolor
            }
          ]
        }
        if (dg.customers > 0) {
          td.children.push(j)
          co++
        }
        ct++
      }
      console.log(td)

      this.parsedTree = td
    },
    sumByCount (d) {
      return d.children ? 0 : 1
    },
    sumBySize (d) {
      return d.count
    },
    cellClicked (d, obh) {
      console.log(d)
      console.log(obh)
      this.selected = d
//      d.attr("class","active")
      let data = {
        field: 'segment',
        bar: {
          key: d.data.id
        }
      }
      d3.select(obh).attr("class","leaf active")
      this.$emit('barclicked', data)
    },
    changed (sum) {

      treemap(root.sum(sum));

      cell.transition()
          .duration(750)
          .attr("transform", function(d) { return "translate(" + d.x0 + "," + d.y0 + ")"; })
        .select("rect")
          .attr("width", function(d) { return d.x1 - d.x0; })
          .attr("height", function(d) { return d.y1 - d.y0; });
    }
  }
}
</script>

<style>
svg {
  margin: 10px;
}

path {
  fill: none;
  stroke: #76BF8A;
  stroke-width: 3px;
}

.active {
  stroke-width: 3;
  stroke: yellow;
}

.text {
  stroke-width: 0.25;
  stroke: black;
}

.leaf {
  cursor: pointer;
}

.cheader {
  text-align: left;
  position: relative;
  left: 10px;
  margin-bottom: -5px;
  font-size: 1.3rem;
}

</style>




